import React, { useState, useEffect, createContext, useContext } from 'react';
import { initializeApp } from 'firebase/app';
import { getAuth, signInAnonymously, onAuthStateChanged } from 'firebase/auth';
import { getFirestore, doc, getDoc, addDoc, setDoc, updateDoc, onSnapshot, collection } from 'firebase/firestore';

// Define Firebase configuration and app ID.
// For GitHub Pages, you MUST hardcode your Firebase config here.
const firebaseConfig = {
    apiKey: "AIzaSyDkJPKUki0-SD2cWiiMJ-JCHhXcZzo-eKc", // YOUR ACTUAL API KEY
    authDomain: "dfwgv-library-manager.firebaseapp.com", // YOUR ACTUAL AUTH DOMAIN
    projectId: "dfwgv-library-manager", // YOUR ACTUAL PROJECT ID
    storageBucket: "dfwgv-library-manager.firebasestorage.app", // YOUR ACTUAL STORAGE BUCKET
    messagingSenderId: "465824512280", // YOUR ACTUAL MESSAGING SENDER ID
    appId: "1:465824512280:web:6684fb67e92c65f6fb466d" // YOUR ACTUAL APP ID
};
const appId = firebaseConfig.projectId; // Use projectId as appId for consistency

// Context for Firebase services and user ID
const FirebaseContext = createContext(null);

// Custom Message Box component instead of alert/confirm
const MessageBox = ({ message, type, onClose, onConfirm }) => {
    if (!message) return null;

    return (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 flex items-center justify-center z-50 p-4">
            <div className="bg-white rounded-lg shadow-xl p-6 max-w-sm w-full">
                <p className="text-gray-800 text-lg mb-4">{message}</p>
                <div className="flex justify-end space-x-3">
                    {type === 'confirm' && (
                        <button
                            onClick={onConfirm}
                            className="px-4 py-2 bg-blue-500 text-white rounded-md hover:bg-blue-600 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-opacity-50"
                        >
                            Confirm
                        </button>
                    )}
                    <button
                        onClick={onClose}
                        className="px-4 py-2 bg-gray-300 text-gray-800 rounded-md hover:bg-gray-400 focus:outline-none focus:ring-2 focus:ring-gray-300 focus:ring-opacity-50"
                    >
                        {type === 'confirm' ? 'Cancel' : 'Close'}
                    </button>
                </div>
            </div>
        </div>
    );
};

// Firebase Provider component
const FirebaseProvider = ({ children }) => {
    const [db, setDb] = useState(null);
    const [auth, setAuth] = useState(null);
    const [userId, setUserId] = useState(null);
    const [loadingFirebase, setLoadingFirebase] = useState(true);

    useEffect(() => {
        const app = initializeApp(firebaseConfig);
        const firestore = getFirestore(app);
        const firebaseAuth = getAuth(app);

        setDb(firestore);
        setAuth(firebaseAuth);

        const unsubscribe = onAuthStateChanged(firebaseAuth, async (user) => {
            if (user) {
                setUserId(user.uid);
            } else {
                // Sign in anonymously if no user is authenticated
                try {
                    await signInAnonymously(firebaseAuth);
                } catch (error) {
                    console.error("Error signing in anonymously:", error);
                }
            }
            setLoadingFirebase(false);
        });

        return () => unsubscribe();
    }, []);

    if (loadingFirebase) {
        return (
            <div className="flex items-center justify-center min-h-screen bg-gray-100">
                <div className="animate-spin rounded-full h-16 w-16 border-t-2 border-b-2 border-blue-500"></div>
                <p className="ml-4 text-lg text-gray-700">Loading application...</p>
            </div>
        );
    }

    return (
        <FirebaseContext.Provider value={{ db, auth, userId }}>
            {children}
        </FirebaseContext.Provider>
    );
};

// Main App Component
const App = () => {
    const { db, userId } = useContext(FirebaseContext);
    const [owner1BggUsername, setOwner1BggUsername] = useState('');
    const [owner2BggUsername, setOwner2BggUsername] = useState('');
    const [owner1Name, setOwner1Name] = useState('Owner A');
    const [owner2Name, setOwner2Name] = useState('Owner B');
    const [games, setGames] = useState([]); // All games in the library
    const [conventions, setConventions] = useState([]);
    const [currentConvention, setCurrentConvention] = useState(null);
    const [newConventionName, setNewConventionName] = useState('');
    const [loading, setLoading] = useState(false);
    const [message, setMessage] = useState('');
    const [messageType, setMessageType] = useState('info');
    const [confirmAction, setConfirmAction] = useState(null);

    // Function to show custom message box
    const showMessage = (msg, type = 'info', onConfirm = null) => {
        setMessage(msg);
        setMessageType(type);
        setConfirmAction(() => onConfirm);
    };

    const closeMessage = () => {
        setMessage('');
        setMessageType('info');
        setConfirmAction(null);
    };

    // Fetch games from BGG API
    const fetchBggCollection = async (username, ownerName) => {
        try {
            // Using a proxy to bypass CORS issues if running locally or on some hosting environments.
            // For BGG's XML API, direct fetch usually works, but a proxy is safer for general use.
            // If deploying to a server that doesn't have a proxy, this might need adjustment.
            const url = `https://boardgamegeek.com/xmlapi2/collection?username=${username}&stats=1`;
            const response = await fetch(url);
            if (!response.ok) {
                throw new Error(`HTTP error! status: ${response.status}`);
            }
            const text = await response.text();
            const parser = new DOMParser();
            const xmlDoc = parser.parseFromString(text, "text/xml");
            const items = xmlDoc.getElementsByTagName('item');

            const newGames = [];
            for (let i = 0; i < items.length; i++) {
                const item = items[i];
                const bggId = item.getAttribute('objectid');
                const name = item.getElementsByTagName('name')[0]?.textContent || 'Unknown Game';
                const thumbnail = item.getElementsByTagName('thumbnail')[0]?.textContent || '';
                const image = item.getElementsByTagName('image')[0]?.textContent || '';
                const stats = item.getElementsByTagName('stats')[0];
                const minPlayers = stats?.getAttribute('minplayers') || 0;
                const maxPlayers = stats?.getAttribute('maxplayers') || 0;
                const playingTime = stats?.getAttribute('playingtime') || 0;

                newGames.push({
                    bggId,
                    name,
                    thumbnail,
                    image,
                    minPlayers: parseInt(minPlayers),
                    maxPlayers: parseInt(maxPlayers),
                    playingTime: parseInt(playingTime),
                    ownerName,
                    ownerId: userId, // The Firebase UID of the user who imported it
                    checkoutCount: 0,
                    isCheckedOut: false,
                    lastCheckedOutBy: '',
                    lastCheckedOutDate: null,
                    lastCheckedInDate: null,
                });
            }
            return newGames;
        } catch (error) {
            console.error(`Error fetching BGG collection for ${username}:`, error);
            showMessage(`Failed to fetch collection for ${ownerName}. Please check the username.`, 'error');
            return [];
        }
    };

    // Import games from BGG and save to Firestore
    const importGames = async () => {
        if (!db || !userId) {
            showMessage("Firebase not initialized. Please wait.", 'error');
            return;
        }
        setLoading(true);
        try {
            let allImportedGames = [];

            if (owner1BggUsername) {
                const games1 = await fetchBggCollection(owner1BggUsername, owner1Name);
                allImportedGames = [...allImportedGames, ...games1];
            }
            if (owner2BggUsername) {
                const games2 = await fetchBggCollection(owner2BggUsername, owner2Name);
                allImportedGames = [...allImportedGames, ...games2];
            }

            if (allImportedGames.length === 0) {
                showMessage("No games imported. Please enter valid BGG usernames.", 'info');
                setLoading(false);
                return;
            }

            // Using a public collection path as per previous instructions for shared data
            const gamesCollectionRef = collection(db, `artifacts/${appId}/public/data/games`);
            
            // Using Promise.all for concurrent writes for efficiency
            const writePromises = allImportedGames.map(async (game) => {
                // Create a unique ID for each game copy, including owner info for duplicates
                const gameDocId = `${game.bggId}_${game.ownerName.replace(/\s/g, '')}`;
                const gameRef = doc(gamesCollectionRef, gameDocId);
                
                // Check if game copy already exists to avoid overwriting checkout data
                const existingDoc = await getDoc(gameRef);
                if (!existingDoc.exists()) {
                    await setDoc(gameRef, game);
                    return { status: 'added', gameName: game.name, owner: game.ownerName };
                } else {
                    return { status: 'skipped', gameName: game.name, owner: game.ownerName };
                }
            });

            const results = await Promise.all(writePromises);
            const addedCount = results.filter(r => r.status === 'added').length;
            const skippedCount = results.filter(r => r.status === 'skipped').length;

            showMessage(`Imported ${addedCount} new games. Skipped ${skippedCount} existing game copies.`, 'info');
        } catch (error) {
            console.error("Error importing games:", error);
            showMessage("Error importing games. Please try again.", 'error');
        } finally {
            setLoading(false);
        }
    };

    // Listen for real-time updates to games and conventions
    useEffect(() => {
        if (!db || !userId) return;

        // Listen for games
        const gamesCollectionRef = collection(db, `artifacts/${appId}/public/data/games`);
        const unsubscribeGames = onSnapshot(gamesCollectionRef, (snapshot) => {
            const fetchedGames = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
            setGames(fetchedGames);
        }, (error) => {
            console.error("Error fetching games:", error);
            showMessage("Failed to load games.", 'error');
        });

        // Listen for conventions
        const conventionsCollectionRef = collection(db, `artifacts/${appId}/public/data/conventions`);
        const unsubscribeConventions = onSnapshot(conventionsCollectionRef, (snapshot) => {
            const fetchedConventions = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
            setConventions(fetchedConventions);
            // Update current convention if its data changes
            if (currentConvention) {
                const updatedCurrent = fetchedConventions.find(conv => conv.id === currentConvention.id);
                if (updatedCurrent) {
                    setCurrentConvention(updatedCurrent);
                }
            }
        }, (error) => {
            console.error("Error fetching conventions:", error);
            showMessage("Failed to load conventions.", 'error');
        });

        return () => {
            unsubscribeGames();
            unsubscribeConventions();
        };
    }, [db, userId, currentConvention]); // Added currentConvention to dependencies to re-evaluate its update logic

    // Create a new convention
    const createConvention = async () => {
        if (!db || !userId) {
            showMessage("Firebase not initialized. Please wait.", 'error');
            return;
        }
        if (!newConventionName.trim()) {
            showMessage("Convention name cannot be empty.", 'info');
            return;
        }
        setLoading(true);
        try {
            const conventionsCollectionRef = collection(db, `artifacts/${appId}/public/data/conventions`);
            await addDoc(conventionsCollectionRef, {
                name: newConventionName,
                date: new Date().toISOString(), // Store date as ISO string for consistency
                games: [], // Initialize with an empty array of games (just IDs and basic info)
                ownerId: userId,
            });
            setNewConventionName('');
            showMessage(`Convention "${newConventionName}" created successfully!`, 'info');
        } catch (error) {
            console.error("Error creating convention:", error);
            showMessage("Error creating convention. Please try again.", 'error');
        } finally {
            setLoading(false);
        }
    };

    // Select a convention to work with
    const selectConvention = (convention) => {
        setCurrentConvention(convention);
        showMessage(`Selected convention: ${convention.name}`, 'info');
    };

    // Add/Remove game from current convention
    const toggleGameForConvention = async (game) => {
        if (!db || !userId || !currentConvention) {
            showMessage("Please select a convention first.", 'info');
            return;
        }

        setLoading(true);
        try {
            const conventionRef = doc(db, `artifacts/${appId}/public/data/conventions`, currentConvention.id);
            const conventionDoc = await getDoc(conventionRef);
            if (!conventionDoc.exists()) {
                showMessage("Convention not found.", 'error');
                setLoading(false);
                return;
            }

            const currentGames = conventionDoc.data().games || [];
            // We store minimal game info in the convention's games array for simplicity and to avoid large documents
            const gameInfoForConvention = {
                id: game.id,
                bggId: game.bggId,
                name: game.name,
                ownerName: game.ownerName,
                thumbnail: game.thumbnail,
            };

            const gameIndex = currentGames.findIndex(g => g.id === game.id);

            let updatedGames;
            let msg;
            if (gameIndex > -1) {
                // Game already in convention, remove it
                updatedGames = currentGames.filter(g => g.id !== game.id);
                msg = `Removed "${game.name} (${game.ownerName})" from ${currentConvention.name}.`;
            } else {
                // Game not in convention, add it
                updatedGames = [...currentGames, gameInfoForConvention];
                msg = `Added "${game.name} (${game.ownerName})" to ${currentConvention.name}.`;
            }

            await updateDoc(conventionRef, { games: updatedGames });
            showMessage(msg, 'info');
        } catch (error) {
            console.error("Error toggling game for convention:", error);
            showMessage("Error updating convention games. Please try again.", 'error');
        } finally {
            setLoading(false);
        }
    };

    // Check out/in a game
    const toggleGameCheckout = async (game) => {
        if (!db || !userId) {
            showMessage("Firebase not initialized. Please wait.", 'error');
            return;
        }
        setLoading(true);
        try {
            const gameRef = doc(db, `artifacts/${appId}/public/data/games`, game.id);
            const newCheckedOutStatus = !game.isCheckedOut;
            const newCheckoutCount = newCheckedOutStatus ? game.checkoutCount + 1 : game.checkoutCount;
            const now = new Date().toISOString();

            await updateDoc(gameRef, {
                isCheckedOut: newCheckedOutStatus,
                checkoutCount: newCheckoutCount,
                lastCheckedOutBy: newCheckedOutStatus ? userId : '',
                lastCheckedOutDate: newCheckedOutStatus ? now : game.lastCheckedOutDate,
                lastCheckedInDate: !newCheckedOutStatus ? now : game.lastCheckedInDate,
            });
            showMessage(`${game.name} (${game.ownerName}) ${newCheckedOutStatus ? 'checked out' : 'checked in'}.`, 'info');
        } catch (error) {
            console.error("Error toggling game checkout:", error);
            showMessage("Error updating game checkout status. Please try again.", 'error');
        } finally {
            setLoading(false);
        }
    };

    // Export current convention games to CSV
    const exportConventionGamesToCsv = async () => {
        if (!db || !currentConvention || !currentConvention.games || currentConvention.games.length === 0) {
            showMessage("No convention selected or no games in the current convention to export.", 'info');
            return;
        }

        setLoading(true);
        try {
            const gamesCollectionRef = collection(db, `artifacts/${appId}/public/data/games`);
            const gameDetailsPromises = currentConvention.games.map(async (convGame) => {
                const gameDoc = await getDoc(doc(gamesCollectionRef, convGame.id));
                if (gameDoc.exists()) {
                    return gameDoc.data();
                }
                return null;
            });

            const detailedGames = (await Promise.all(gameDetailsPromises)).filter(Boolean);

            const headers = ["Game Name", "Owner", "BGG ID", "Current Checkout Status", "Total Checkouts"];
            const rows = detailedGames.map(game => [
                `"${game.name.replace(/"/g, '""')}"`,
                `"${game.ownerName.replace(/"/g, '""')}"`,
                game.bggId,
                game.isCheckedOut ? "Checked Out" : "Available",
                game.checkoutCount,
            ]);

            const csvContent = [
                headers.join(','),
                ...rows.map(row => row.join(','))
            ].join('\n');

            const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
            const link = document.createElement('a');
            if (link.download !== undefined) {
                const url = URL.createObjectURL(blob);
                link.setAttribute('href', url);
                link.setAttribute('download', `${currentConvention.name.replace(/\s/g, '_')}_Games.csv`);
                link.style.visibility = 'hidden';
                document.body.appendChild(link);
                link.click();
                document.body.removeChild(link);
                showMessage("CSV exported successfully!", 'info');
            } else {
                showMessage("Your browser does not support downloading files directly. Please copy the content manually.", 'error');
            }
        } catch (error) {
            console.error("Error exporting CSV:", error);
            showMessage("Error exporting CSV. Please try again.", 'error');
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="min-h-screen bg-gray-100 text-gray-900 font-sans flex flex-col items-center p-4 sm:p-6">
            <style>
                {`
                @import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap');
                body {
                    font-family: 'Inter', sans-serif;
                }
                .scrollable-list {
                    max-height: 400px;
                    overflow-y: auto;
                    border: 1px solid #e2e8f0;
                    border-radius: 0.5rem;
                }
                `}
            </style>

            <h1 className="text-4xl font-bold text-blue-700 mb-8 mt-4">DFW Gaming Village Library Manager</h1>
            <p className="text-sm text-gray-600 mb-6">Your User ID: <span className="font-mono text-blue-600 break-all">{userId}</span></p>

            {loading && (
                <div className="fixed inset-0 bg-gray-200 bg-opacity-75 flex items-center justify-center z-50">
                    <div className="animate-spin rounded-full h-20 w-20 border-t-4 border-b-4 border-blue-600"></div>
                    <p className="ml-4 text-xl text-blue-700">Loading...</p>
                </div>
            )}

            <MessageBox message={message} type={messageType} onClose={closeMessage} onConfirm={confirmAction} />

            {/* BGG Import Section */}
            <section className="bg-white p-6 rounded-xl shadow-lg mb-8 w-full max-w-4xl">
                <h2 className="text-2xl font-semibold text-blue-600 mb-4">Import BoardGameGeek Collections</h2>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-6">
                    <div>
                        <label htmlFor="owner1Name" className="block text-sm font-medium text-gray-700 mb-1">Owner 1 Name</label>
                        <input
                            type="text"
                            id="owner1Name"
                            className="w-full p-3 border border-gray-300 rounded-md focus:ring-blue-500 focus:border-blue-500"
                            value={owner1Name}
                            onChange={(e) => setOwner1Name(e.target.value)}
                            placeholder="e.g., John Doe"
                        />
                        <label htmlFor="owner1Bgg" className="block text-sm font-medium text-gray-700 mt-3 mb-1">Owner 1 BGG Username</label>
                        <input
                            type="text"
                            id="owner1Bgg"
                            className="w-full p-3 border border-gray-300 rounded-md focus:ring-blue-500 focus:border-blue-500"
                            value={owner1BggUsername}
                            onChange={(e) => setOwner1BggUsername(e.target.value)}
                            placeholder="e.g., bgg_user_one"
                        />
                    </div>
                    <div>
                        <label htmlFor="owner2Name" className="block text-sm font-medium text-gray-700 mb-1">Owner 2 Name</label>
                        <input
                            type="text"
                            id="owner2Name"
                            className="w-full p-3 border border-gray-300 focus:ring-blue-500 focus:border-blue-500"
                            value={owner2Name}
                            onChange={(e) => setOwner2Name(e.target.value)}
                            placeholder="e.g., Jane Smith"
                        />
                        <label htmlFor="owner2Bgg" className="block text-sm font-medium text-gray-700 mt-3 mb-1">Owner 2 BGG Username</label>
                        <input
                            type="text"
                            id="owner2Bgg"
                            className="w-full p-3 border border-gray-300 rounded-md focus:ring-blue-500 focus:border-blue-500"
                            value={owner2BggUsername}
                            onChange={(e) => setOwner2BggUsername(e.target.value)}
                            placeholder="e.g., bgg_user_two"
                        />
                    </div>
                </div>
                <button
                    onClick={importGames}
                    className="w-full bg-green-600 text-white py-3 px-6 rounded-lg text-lg font-semibold hover:bg-green-700 transition duration-300 ease-in-out shadow-md"
                    disabled={loading}
                >
                    Import Games from BoardGameGeek
                </button>
            </section>

            {/* Convention Management Section */}
            <section className="bg-white p-6 rounded-xl shadow-lg mb-8 w-full max-w-4xl">
                <h2 className="text-2xl font-semibold text-blue-600 mb-4">Manage Conventions</h2>
                <div className="flex flex-col sm:flex-row gap-4 mb-6">
                    <input
                        type="text"
                        className="flex-grow p-3 border border-gray-300 rounded-md focus:ring-blue-500 focus:border-blue-500"
                        placeholder="New Convention Name"
                        value={newConventionName}
                        onChange={(e) => setNewConventionName(e.target.value)}
                    />
                    <button
                        onClick={createConvention}
                        className="bg-blue-600 text-white py-3 px-6 rounded-lg font-semibold hover:bg-blue-700 transition duration-300 ease-in-out shadow-md"
                        disabled={loading}
                    >
                        Create Convention
                    </button>
                </div>

                <h3 className="text-xl font-medium text-gray-700 mb-3">Existing Conventions:</h3>
                <div className="scrollable-list bg-gray-50 p-3">
                    {conventions.length === 0 ? (
                        <p className="text-gray-500">No conventions created yet.</p>
                    ) : (
                        <ul className="space-y-2">
                            {conventions.map(conv => (
                                <li key={conv.id} className="flex items-center justify-between bg-white p-3 rounded-md shadow-sm border border-gray-200">
                                    <span className="font-medium text-gray-800">{conv.name} ({new Date(conv.date).toLocaleDateString()})</span>
                                    <button
                                        onClick={() => selectConvention(conv)}
                                        className={`px-4 py-2 rounded-md font-semibold transition duration-300 ease-in-out ${currentConvention?.id === conv.id ? 'bg-blue-100 text-blue-700' : 'bg-gray-200 text-gray-800 hover:bg-gray-300'}`}
                                    >
                                        {currentConvention?.id === conv.id ? 'Selected' : 'Select'}
                                    </button>
                                </li>
                            ))}
                        </ul>
                    )}
                </div>

                {currentConvention && (
                    <div className="mt-6 p-4 bg-blue-50 rounded-lg border border-blue-200">
                        <h3 className="text-xl font-semibold text-blue-700 mb-3">Currently Managing: {currentConvention.name}</h3>
                        <p className="text-gray-700 mb-4">Games for this convention: {currentConvention.games?.length || 0}</p>
                        <button
                            onClick={exportConventionGamesToCsv}
                            className="bg-purple-600 text-white py-2 px-5 rounded-lg font-semibold hover:bg-purple-700 transition duration-300 ease-in-out shadow-md"
                            disabled={loading || !currentConvention.games || currentConvention.games.length === 0}
                        >
                            Export Convention Games to CSV
                        </button>
                        <div className="mt-4 scrollable-list bg-white p-3">
                            {currentConvention.games && currentConvention.games.length > 0 ? (
                                <ul className="space-y-2">
                                    {currentConvention.games.map(game => (
                                        <li key={game.id} className="flex items-center gap-3 bg-gray-100 p-2 rounded-md shadow-sm">
                                            <img src={game.thumbnail || `https://placehold.co/50x50/e2e8f0/64748b?text=No+Img`} alt={game.name} className="w-12 h-12 object-cover rounded-md" />
                                            <span className="font-medium text-gray-800 flex-grow">{game.name} <span className="text-sm text-gray-600">({game.ownerName})</span></span>
                                            {/* Note: Checkout status and count here might not be real-time if not fetched with convention data.
                                                CSV export will fetch fresh data for accuracy. */}
                                            <button
                                                onClick={() => toggleGameForConvention(game)}
                                                className="px-3 py-1 bg-red-500 text-white rounded-md text-sm hover:bg-red-600 transition duration-300 ease-in-out"
                                            >
                                                Remove
                                            </button>
                                        </li>
                                    ))}
                                </ul>
                            ) : (
                                <p className="text-gray-500">No games added to this convention yet.</p>
                            )}
                        </div>
                    </div>
                )}
            </section>

            {/* Game Library & Checkout Section */}
            <section className="bg-white p-6 rounded-xl shadow-lg mb-8 w-full max-w-4xl">
                <h2 className="text-2xl font-semibold text-blue-600 mb-4">Your Combined Board Game Library</h2>
                <p className="text-gray-700 mb-4">Total Games: {games.length}</p>
                <div className="scrollable-list bg-gray-50 p-3">
                    {games.length === 0 ? (
                        <p className="text-gray-500">No games imported yet. Use the section above to import from BGG.</p>
                    ) : (
                        <ul className="space-y-3">
                            {games.map(game => (
                                <li key={game.id} className="flex flex-col sm:flex-row items-start sm:items-center gap-4 bg-white p-4 rounded-md shadow-sm border border-gray-200">
                                    <img src={game.thumbnail || `https://placehold.co/80x80/e2e8f0/64748b?text=No+Img`} alt={game.name} className="w-20 h-20 object-cover rounded-md flex-shrink-0" />
                                    <div className="flex-grow">
                                        <h3 className="text-lg font-semibold text-gray-900">{game.name}</h3>
                                        <p className="text-sm text-gray-600">Owned by: <span className="font-medium text-blue-600">{game.ownerName}</span></p>
                                        <p className="text-sm text-gray-600">Players: {game.minPlayers}-{game.maxPlayers} | Playtime: {game.playingTime} min</p>
                                        <p className="text-sm text-gray-600">
                                            Status: <span className={`font-semibold ${game.isCheckedOut ? 'text-red-600' : 'text-green-600'}`}>
                                                {game.isCheckedOut ? 'Checked Out' : 'Available'}
                                            </span>
                                            <span className="ml-2">| Checkouts: {game.checkoutCount}</span>
                                        </p>
                                    </div>
                                    <div className="flex flex-col sm:flex-row gap-2 mt-3 sm:mt-0">
                                        <button
                                            onClick={() => toggleGameCheckout(game)}
                                            className={`px-4 py-2 rounded-lg font-semibold transition duration-300 ease-in-out shadow-md
                                                ${game.isCheckedOut ? 'bg-red-500 hover:bg-red-600' : 'bg-green-500 hover:bg-green-600'} text-white`}
                                            disabled={loading}
                                        >
                                            {game.isCheckedOut ? 'Check In' : 'Check Out'}
                                        </button>
                                        <button
                                            onClick={() => toggleGameForConvention(game)}
                                            className={`px-4 py-2 rounded-lg font-semibold transition duration-300 ease-in-out shadow-md
                                                ${currentConvention?.games?.some(g => g.id === game.id) ? 'bg-yellow-500 hover:bg-yellow-600' : 'bg-blue-500 hover:bg-blue-600'} text-white`}
                                            disabled={loading || !currentConvention}
                                        >
                                            {currentConvention?.games?.some(g => g.id === game.id) ? 'Remove from Convention' : 'Add to Convention'}
                                        </button>
                                    </div>
                                </li>
                            ))}
                        </ul>
                    )}
                </div>
            </section>
        </div>
    );
};

// Root component to wrap App with FirebaseProvider
const Root = () => (
    <FirebaseProvider>
        <App />
    </FirebaseProvider>
);

export default Root;
